 'use client';

import { useState, useEffect } from "react";
import { db, storage } from "../../lib/firebase";
import { doc, getDoc } from "firebase/firestore";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";

const ProfilePage = ({ user }) => {  // ✅ Renamed to ProfilePage
  const [profile, setProfile] = useState({
    name: "",
    email: "",
  });
  const [imageFile, setImageFile] = useState(null);
  const [resumeFile, setResumeFile] = useState(null);
  const [previewImage, setPreviewImage] = useState("");
  const [resumeUrl, setResumeUrl] = useState("");

  // Fetch user data from Firestore
  useEffect(() => {
    if (!user?.uid) return;  
    const fetchProfile = async () => {
      const docRef = doc(db, "users", user.uid);
      const docSnap = await getDoc(docRef);
      if (docSnap.exists()) {
        setProfile(docSnap.data());
      }
    };
    fetchProfile();
  }, [user]);

  // Handle file uploads
  const handleFileUpload = async (file, type) => {
    if (!file || !user?.uid) return null;
    const fileRef = ref(storage, `users/${user.uid}/${type}`);
    await uploadBytes(fileRef, file);
    return await getDownloadURL(fileRef);
  };

  return (
    <div className="max-w-lg mx-auto p-6 bg-white shadow-md rounded-lg">
      <h2 className="text-2xl font-semibold mb-4">Profile</h2>

      {/* Profile Image */}
      <div className="mb-4">
        <img
          src={previewImage || "https://via.placeholder.com/150"}
          alt="Profile"
          className="w-32 h-32 rounded-full object-cover border-2 border-gray-300"
        />
        <input type="file" accept="image/*" onChange={(e) => setImageFile(e.target.files[0])} />
      </div>

      {/* User Info */}
      <div className="mb-4">
        <input
          type="text"
          className="w-full p-2 border rounded"
          placeholder="Name"
          value={profile.name}
          onChange={(e) => setProfile({ ...profile, name: e.target.value })}
        />
      </div>
      <div className="mb-4">
        <input
          type="email"
          className="w-full p-2 border rounded"
          placeholder="Email"
          value={profile.email}
          onChange={(e) => setProfile({ ...profile, email: e.target.value })}
        />
      </div>

      {/* Resume Upload */}
      <div className="mb-4">
        <label className="block">Resume (PDF only):</label>
        <input type="file" accept=".pdf" onChange={(e) => setResumeFile(e.target.files[0])} />
        {resumeUrl && <a href={resumeUrl} target="_blank" rel="noopener noreferrer" className="text-blue-500">View Resume</a>}
      </div>

      {/* Save Button */}
      <button onClick={() => alert("Profile Updated (Files not stored)")} className="w-full bg-blue-500 text-white p-2 rounded">
        Save Profile
      </button>
    </div>
  );
};

// ✅ Ensure it's a default export for a page in App Router
export default ProfilePage;
